import style from './Main.module.css';
import Input from '@mui/material/Input';
import { useRef, useState, useCallback } from 'react';
import Todo from '../Todo';

const Main = () => {
  const id = useRef(1640018213435); //可持有可變(mutable)的值，且具有current屬性，透過.current更改值不會re-render
  const [value, setValue] = useState('');
  const [deleteTodo, setDeleteTodo] = useState(false);
  const [completed, setCompleted] = useState(false);
  const [todos, setTodos] = useState([{ id: 1, content: '你好', completed: false }]);
  console.log('Render Main!!!!!');

  const handleInputChange = e => {
    setValue(e.target.value);
  };

  const handleAddTodo = e => {
    e.preventDefault();
    id.current = new Date().getTime();
    const newTasks = [...todos,{ id: id.current, content: value, completed: false }]
    setTodos(newTasks);
    setValue('');
  };

  const handleRemoveTodo = useCallback(index => {
    console.log('handleRemoveTodo');
    setDeleteTodo(prevdeleteTodo => !prevdeleteTodo);
    const filterRemoveItem = todos.filter((todo, newIndex) => newIndex !== index)
    setTodos(filterRemoveItem);
  },[todos])

  const completeTask = useCallback(index => {
    console.log('completeTask');
    setCompleted(prevcompleted => !prevcompleted);
    const newTasks = [...todos];
    newTasks[index].completed = !completed;
    setTodos(newTasks);
  },[todos, completed])

  return (
    <main className={style.main}>
      <form onSubmit={handleAddTodo}>
        <div className={style.inputreg}>
        <Input placeholder="Here Add todo" value={value} onChange={e => handleInputChange(e)} className={style.todoinput} type="text" id="todo-input" />
        <button type="submit" className={style.addbtn}>
          ADD ITEM
        </button>
        </div>
      </form>
      <div className={style.todolist}>
          { todos.map((todo, index) =>
           <Todo 
            deleteTodo={deleteTodo}
            handleRemoveTodo={handleRemoveTodo}
            completeTask={completeTask}
            key={todo.id}
            index={index}
            todo={todo} />)
          }
      </div>
    </main>
  );
}

export default Main;
