import React, { memo } from "react";
import style from './Todo.module.css';
import Checkbox from '@mui/material/Checkbox';
import Delete from '@mui/icons-material/Delete';
import classNames from 'classnames';

 //使用 React.memo()當props沒有改變就不re-render，對於props是物件型別可以使用第二個參數自訂義比較props的方法，回傳false會re-render
const Todo = memo(({handleRemoveTodo, completeTask, index, todo}) => {//使用解構把props這個物件內的值取出來(Props come from the Parent Component )
  console.log('Render Todo!!!!!');

  const done = classNames({
    [`${style.todocontent}`]: true,
    [`${style.todoisdone}`]: todo.completed
  })

  return (
    <ul className={style.todogroup}>
      <li className={style.todoli}>
        <span className={done}>{todo.content}</span>
        <div className={style.action}>
          <Checkbox checked={todo.completed} onChange={() => completeTask(index)}  className={style.todoicon}>isCheck</Checkbox>
          <Delete onClick={() => handleRemoveTodo(index)} className={style.todoicon} >delete</Delete>
        </div>
      </li>
    </ul>
  );
});

export default Todo;
